import {Button, Form} from "antd";
import {withTranslation} from "react-i18next";
import CellEditTable from "../../common/Table/CellEditTable";
import React from "react";
import {PlusOutlined} from "@ant-design/icons";

const SubjectiveConfigForm = (props: any) => {
    const tableColumns = [
        {
            title: '',
            dataIndex: 'name',
            editable: true,
            valueType: 'text'
        },
        {
            title: '',
            dataIndex: 'score',
            editable: true,
            valueType: 'digit'
        },
        {
            title: "",
            dataIndex: "answer",
            editable: true,
            valueType: 'textarea'
        }
    ]
    return (
        <>
            <Form.Item name={["config", "judgeConfig"]} label={""}>
                <CellEditTable
                    columns={tableColumns}
                    rowKey={"id"}
                    toolBar={(actionRef: any) => {
                        return [
                            <Button
                                type={"dashed"}
                                block
                                icon={<PlusOutlined/>}
                                onClick={() => {
                                    actionRef.current?.addEditRecord?.({
                                        id: Date.now()
                                    }, {newRecordType: "dataSource"});
                                }}
                            >{""}</Button>
                        ]
                    }}
                    rowButton={(row: any, value: any, onChange: any) => {
                        return []
                    }}
                />
            </Form.Item>
        </>
    )
}

export default withTranslation()(SubjectiveConfigForm)
